// 引入通用table组件
import VXETable from '@/components/common/Table.vue';
import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import { TITLE, MSG, STATUS_CODE, BTN_TEXT, COMPANY, CONSTANTS_USER, VARIABLE } from '@/utils/constantsFile';
// 导入所需接口
import {
  companyGetCompanyListAction, companyUpdateByIdAction
} from '@/api/management';
export default {
  components: {
    // 加载通用table组件
    VXETable
  },
  data() {
    return {
      VARIABLE: VARIABLE,
      COMPANY: COMPANY,
      BTN_TEXT: BTN_TEXT,
      CONSTANTS_USER: CONSTANTS_USER,
      // table是否加载
      tableLoading: false,
      // 修改对话框是否可见，默认为false，表示不可见
      dialogVisible: false,
      formLabelWidth: '130px',
      // 暂存修改后的row数据
      tempRow: {},
      titleName: COMPANY.MODIFY_COMPANY_INFO,
      // table数据
      tableDatas: [],
      // 审核状态下拉
      stateOptions: [
        {
          label: COMPANY.AUDIT_PASS,
          value: COMPANY.ONE_NUMBER
        },
        // {
        //   label: COMPANY.NOT_AUDIT,
        //   value: COMPANY.TWO_NUMBER
        // },
        {
          label: COMPANY.STOP,
          value: COMPANY.THREE_NUMBER
        },
        {
          label: COMPANY.AUDIT_NOT_PASS,
          value: COMPANY.FOUR_NUMBER
        }
      ],
      rules: {
        companyName: [
          {
            required: true,
            trigger: 'blur',
            message: MSG.PLEASE_ENTER_CONTENT
          },
          { max: 20, message: '长度不得大于20个字符' },
          { pattern: /^[A-Za-z0-9\u4e00-\u9fa5]+$/, message: '不允许输入空格等特殊符号' }
        ]
        // realName: [
        //   {
        //     required: true,
        //     trigger: 'blur',
        //     message: MSG.PLEASE_ENTER_CONTENT
        //   },
        //   { max: 10, message: '长度不得大于10个字符' },
        //   { pattern: /^[A-Za-z0-9\u4e00-\u9fa5]+$/, message: '不允许输入空格等特殊符号' }
        // ],
        // telephoneNumber: [
        //   {
        //     required: true,
        //     trigger: 'blur',
        //     message: MSG.PLEASE_ENTER_CONTENT
        //   },
        //   { max: 11, message: '长度不得大于11个字符' }
        // ],
        // email: [
        //   {
        //     required: true,
        //     type: 'email',
        //     message: CONSTANTS_USER.RULES_EMAIL,
        //     trigger: ['blur']
        //   },
        //   { max: 20, message: '长度不得大于20个字符' }
        // ]
        // state: [
        //   {
        //     trigger: 'change'
        //     // message: MSG.PLEASE_CHOOSE_CONTENT
        //   }
        // ]
      },
      // table表头配置
      tableColumns: [
        {
          type: 'seq',
          width: '80',
          title: VARIABLE.SEQ,
          fixed: 'left',
          align: 'center'
        },
        {
          field: 'companyName',
          title: COMPANY.COMPANY_NAME,
          width: '',
          align: 'center'
        },
        {
          field: 'realName',
          title: COMPANY.REAL_NAME,
          width: '',
          align: 'center'
        },
        {
          field: 'telephoneNumber',
          title: COMPANY.TELEPHONE_NUMBER,
          width: '',
          align: 'center'
        },
        {
          field: 'email',
          title: COMPANY.EMAIL,
          width: '',
          align: 'center'
        },
        {
          field: 'state',
          title: COMPANY.STATE,
          width: '150',
          align: 'center',
          formatter: ({ cellValue, row, column }) => {
            if (row.state === 1) {
              return COMPANY.AUDIT_PASS;
            } else if (row.state === 4) {
              return COMPANY.AUDIT_NOT_PASS;
            } else if (row.state === 2) {
              return COMPANY.NOT_AUDIT;
            } else if (row.state === 3) {
              return COMPANY.STOP;
            }
          }
        },
        {
          field: 'updateTime',
          title: COMPANY.UPDATE_TIME,
          width: '200',
          align: 'center',
          formatter: 'formatDate',
          editRender: {
            props: { type: 'datetime', format: 'yyyy-MM-dd HH:mm:ss' }
          }
        },
        {
          field: '',
          title: BTN_TEXT.OPERATION,
          width: '90',
          fixed: 'right',
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              return [
                <el-button onClick={() => this.toUpdate(row)} type="text">
                  {BTN_TEXT.MODIFY}
                </el-button>
              ];
            }
          }
        }
      ],
      // table分页配置
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      }
    };
  },
  created() {
    // 调用列表查询接口
    this.findList();
  },
  methods: {
    toUpdate(row) {
      // 执行弹出模态框时扩展字段和验证处理
      // this.toSearchRole();
      this.dialogVisible = true;
      // 保存row修改过的数据
      this.tempRow = JSON.parse(JSON.stringify(row));
      // console.log(this.tempRow);
      // const form = JSON.parse(JSON.stringify(this.tempRow));
      // console.log(form);
      // 保存row的初始数据，用于重置
      this.initRow = row;
    },
    // 模态框取消方法
    cancelBtnClick() {
      this.$refs.editCodeForm.resetFields();
      this.dialogVisible = false;
    },
    /**
     * @description: 提交编辑后的行数据
     * @param {null} 无
     * @return: null
     */
    submitEdit() {
      this.$refs.editCodeForm.validate((valid) => {
        if (valid) {
          this.setSubmitEdit();
        }
      });
    },
    async setSubmitEdit() {
      try {
        const form = JSON.parse(JSON.stringify(this.tempRow));
        delete form.createTime;
        delete form.updateTime;
        delete form.extendPropertyValueVoList;
        // delete form.telephoneNumber;
        delete form._XID;
        // delete form.isNewPassword;
        // if (form.newPassword !== '') {
        //   form.password = form.newPassword;
        // }
        // 获取响应结果
        console.log(form);
        const res = await companyUpdateByIdAction(form);
        console.log(res);
        // 响应为true，表示修改提交成功
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 弹出消息提示
          showSuccessMessage(res.message);
          // 隐藏对话框
          this.dialogVisible = false;
          this.initRow = null;
          // this.setUserReleRole();
          // 调用表格初始化方法，获取修改后的数据
          // setTimeout(() => {
          //   if (this.extendPropertyValueVoList.length > 0) {
          //     this.saveExtendValue();
          //   } else {
          //     this.findTableList();
          //   }
          // }, 0);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        this.$notify.error({
          title: TITLE.ERROR,
          message: error,
          showClose: false,
          duration: 2000,
          position: 'bottom-right'
        });
      }
    },
    /**
    * @description: 重置编辑，恢复最初的数据
    * @param {null} null
    * @return: null
    */
    resetEdit() {
      this.tempRow = { ...this.initRow };
      // this.showDialog(this.initRow);
      this.$refs.editCodeForm.resetFields();
    },
    /**
     * @description: 查询未审核公司
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async findList() {
      try {
        this.tableLoading = true;
        const res = await companyGetCompanyListAction(
          {
            size: this.tablePage.pageSize,
            current: this.tablePage.currentPage
          },
          {
            conditionJson1: [{
              column: 'state', // 参数名
              operator: 1, // 并且
              ptype: 'string', // 参数类型
              type: 'in', // 包含
              value: '1,3' // 参数值
            }]
          }
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入table
          this.tableDatas = res.rows;
          // 将总条数写入table分页配置
          this.tablePage.total = res.total;
          // 加载动画消失
          this.tableLoading = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 执行table分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: ZJM
     */
    onTablePageChange(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePage.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePage.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    }
  }
};
