// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入未审核组件
import notAudit from '@/views/company/CompanyAudit/notAudit.vue';
// 引入审核通过组件
import auditPass from '@/views/company/CompanyAudit/auditPass.vue';
// 引入审核未通过组件
import auditNotPass from '@/views/company/CompanyAudit/auditNotPass.vue';
import { MSG, COMPANY } from '@/utils/constantsFile';
export default {
  name: 'CompanyAudit',
  data() {
    return {
      MSG: MSG,
      // 操作执行
      operationLoad: false,
      // tab默认激活下标
      active: 0,
      // 默认引入的组件名称
      currentView: 'notAudit',
      tabs: [
        {
          type: 'notAudit',
          name: COMPANY.NOT_AUDIT,
          view: 'notAudit'
        },
        {
          type: 'auditPass',
          name: COMPANY.AUDIT_PASS,
          view: 'auditPass'
        },
        {
          type: 'auditNotPass',
          name: COMPANY.AUDIT_NOT_PASS,
          view: 'auditNotPass'
        }
      ]
    };
  },
  methods: {
    /**
     * @description: 切换tab事件
     * @param i 下标
     * @param v 组件名
     * @return: 无
     * @author: ZJM
     */
    toggle(i, v) {
      this.active = i;
      this.currentView = v;
    },
    /**
     * @description: 更改操作执行状态
     * @param param 切换是否显示操作执行状态
     * @return: 无
     * @author: ZJM
     */
    changeLoad(param) {
      this.operationLoad = param;
    }
  },
  components: {
    HeaderTitle,
    // 加载未审核组件
    notAudit,
    // 加载审核通过组件
    auditPass,
    // 加载审核未通过组件
    auditNotPass
  }
};
