// 引入通用table组件
import VXETable from '@/components/common/Table.vue';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import { MSG, BTN_TEXT, TITLE, STATUS_CODE, COMPANY, VARIABLE } from '@/utils/constantsFile';
// 导入日期处理类库
import moment from 'moment';
// 导入所需接口
import {
  companyGetCompanyListAction,
  companyVerifyPassAction,
  companyUpdateByIdAction,
  companyInsertOrUpdateBatchAction,
  userInitPowerAction
} from '@/api/management';
export default {
  components: {
    // 加载通用table组件
    VXETable,
    HeadOperationItem
  },
  data() {
    return {
      BTN_TEXT: BTN_TEXT,
      dataChecks: [],
      // table是否加载
      tableLoading: false,
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [
        {
          type: 'checkbox',
          width: '50',
          fixed: 'left',
          align: 'center'
        },
        {
          type: 'seq',
          width: '80',
          title: VARIABLE.SEQ,
          fixed: 'left',
          align: 'center'
        },
        {
          field: 'companyName',
          title: COMPANY.COMPANY_NAME,
          width: '',
          align: 'center'
        },
        {
          field: 'realName',
          title: COMPANY.REAL_NAME,
          width: '',
          align: 'center'
        },
        {
          field: 'telephoneNumber',
          title: COMPANY.TELEPHONE_NUMBER,
          width: '',
          align: 'center'
        },
        {
          field: 'email',
          title: COMPANY.EMAIL,
          width: '',
          align: 'center'
        },
        {
          field: '',
          title: BTN_TEXT.OPERATION,
          width: '230',
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              return [
                <div>
                  <el-button
                    type="text"
                    onClick={() => this.success(row, column)}
                  >
                    {COMPANY.AUDIT_PASS}
                  </el-button>
                  <el-button
                    type="text"
                    onClick={() => this.unsuccess(row, column)}
                  >
                    {COMPANY.AUDIT_NOT_PASS}
                  </el-button>
                </div>
              ];
            }
          }
        }
      ],
      // table分页配置
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true,
        slots: {
          // 使用自定义插槽
          left: 'pager_left'
        }
      }
    };
  },
  created() {
    // 调用列表查询接口
    this.findList();
  },
  methods: {
    deteleall() {
      if (this.dataChecks.length === 0) {
        this.$message.error(MSG.PLEASE_CHOOSE_DELETE);
      } else {
        const arrdelete = this.dataChecks.map((item) => {
          var json = {};
          json.companyId = item.companyId;
          json.state = 4;
          return json;
        });

        this.$confirm(MSG.CONFIRM_CHOOSE_DELETE, TITLE.PROMPT, {
          confirmButtonText: BTN_TEXT.CONFIRM,
          cancelButtonText: BTN_TEXT.CANCEL,
          type: 'warning'
        })
          .then(() => {
            this.setDeleteChecks(arrdelete);
          })
          .catch(() => {
          });
      }
    },
    async setDeleteChecks(param) {
      try {
        const res = await companyInsertOrUpdateBatchAction({}, param);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          showSuccessMessage(res.message);
          this.findList();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    tableChooseCheck(data) {
      this.dataChecks = data;
    },
    /**
     * @description: 查询未审核公司
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async findList() {
      try {
        this.tableLoading = true;
        const res = await companyGetCompanyListAction(
          {
            size: this.tablePage.pageSize,
            current: this.tablePage.currentPage
          },
          {
            conditionJson1: [{
              column: 'state', // 参数名
              operator: 1, // 并且
              ptype: 'string', // 参数类型
              type: 'in', // 包含
              value: '2' // 参数值
            }]
          }
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入table
          this.tableDatas = res.rows;
          // 将总条数写入table分页配置
          this.tablePage.total = res.total;
          // 加载动画消失
          this.tableLoading = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 执行table分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: ZJM
     */
    onTablePageChange(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePage.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePage.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    },
    /**
     * @description: 点击审核通过
     * @param row 当前行数据
     * @param column 表头内容
     * @return: 无
     * @author: ZJM
     */
    success(row, column) {
      // 确认是否执行操作
      this.$confirm(COMPANY.IS_PASS, TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          // 调用执行审核通过的方法
          this.setSuccess(row);
        })
        .catch(() => { });
    },
    /**
     * @description: 执行审核通过的方法
     * @param row  当前行数据
     * @return: 无
     * @author: ZJM
     */
    async setSuccess(row) {
      try {
        this.$emit('changeLoad', true);
        const res = await companyVerifyPassAction({
          companyId: row.companyId
        });
        if (res.code) {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            var id = row.userId;
            this.initPower(row, id);
          } else {
            // 提示异常信息
            this.$emit('changeLoad', false);
            showWarningMessage(res.message);
          }
        }
      } catch (error) {
        this.$emit('changeLoad', false);
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    async initPower(row, id) {
      try {
        const res = await userInitPowerAction({
          userId: id
        });
        if (res.code) {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            // 调用列表查询接口
            // 提示异常信息
            this.$emit('changeLoad', false);
            showSuccessMessage(res.message);
            this.findList();
          }
        }
      } catch (error) {
      } finally {
      }
    },
    /**
     * @description: 点击审核不通过
     * @param row 当前行数据
     * @param column 表头内容
     * @return: 无
     * @author: ZJM
     */
    unsuccess(row, column) {
      // 确认是否执行操作
      this.$confirm(COMPANY.NOT_PASS, TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          // 调用执行审核不通过的方法
          this.setUnsuccess(row);
        })
        .catch(() => { });
    },
    /**
     * @description: 执行审核不通过的方法
     * @param row  当前行数据
     * @return: 无
     * @author: ZJM
     */
    async setUnsuccess(row) {
      try {
        this.tableLoading = true;
        const res = await companyUpdateByIdAction({
          companyId: row.companyId,
          updateTime: moment(new Date().getTime()).format('YYYY-MM-DD HH:mm:ss'),
          state: 4
        });
        if (res.code) {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            // 调用列表查询接口
            this.findList();
            // 提示异常信息
            showSuccessMessage(res.message);
          } else {
            // 提示异常信息
            showWarningMessage(res.message);
          }
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    }
  }
};
