// 引入通用table组件
import VXETable from '@/components/common/Table.vue';
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import { TITLE, STATUS_CODE, COMPANY, VARIABLE } from '@/utils/constantsFile';
// 导入所需接口
import {
  companyGetCompanyListAction
} from '@/api/management';
export default {
  components: {
    // 加载通用table组件
    VXETable
  },
  data() {
    return {
      // table是否加载
      tableLoading: false,
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [
        {
          type: 'seq',
          width: '80',
          title: VARIABLE.SEQ,
          fixed: 'left',
          align: 'center'
        },
        {
          field: 'companyName',
          title: COMPANY.COMPANY_NAME,
          width: '',
          align: 'center'
        },
        {
          field: 'realName',
          title: COMPANY.REAL_NAME,
          width: '',
          align: 'center'
        },
        {
          field: 'telephoneNumber',
          title: COMPANY.TELEPHONE_NUMBER,
          width: '',
          align: 'center'
        },
        {
          field: 'email',
          title: COMPANY.EMAIL,
          width: '',
          align: 'center'
        },
        {
          field: 'state',
          title: COMPANY.STATE,
          width: '150',
          align: 'center',
          formatter: ({ cellValue, row, column }) => {
            if (row.state === 1) {
              return COMPANY.AUDIT_PASS;
            } else if (row.state === 4) {
              return COMPANY.AUDIT_NOT_PASS;
            } else if (row.state === 2) {
              return COMPANY.NOT_AUDIT;
            } else if (row.state === 3) {
              return COMPANY.STOP;
            }
          }
        },
        {
          field: 'updateTime',
          title: COMPANY.UPDATE_TIME,
          width: '200',
          align: 'center',
          formatter: 'formatDate',
          editRender: {
            props: { type: 'datetime', format: 'yyyy-MM-dd HH:mm:ss' }
          }
        }
      ],
      // table分页配置
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      }
    };
  },
  created() {
    // 调用列表查询接口
    this.findList();
  },
  methods: {
    /**
     * @description: 查询未审核公司
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async findList() {
      try {
        this.tableLoading = true;
        const res = await companyGetCompanyListAction(
          {
            size: this.tablePage.pageSize,
            current: this.tablePage.currentPage
          },
          {
            conditionJson1: [{
              column: 'state', // 参数名
              operator: 1, // 并且
              ptype: 'string', // 参数类型
              type: 'in', // 包含
              value: '4' // 参数值
            }]
          }
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入table
          this.tableDatas = res.rows || [];
          // 将总条数写入table分页配置
          this.tablePage.total = res.total || 0;
          // 加载动画消失
          this.tableLoading = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 执行table分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: ZJM
     */
    onTablePageChange(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePage.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePage.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    }
  }
};
